<template>
  <div>
    <!--  <headerBar :header-data="headerData" /> -->
    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goShGzrzYl()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">审核记录</div>
      <div class="nav-bar-right" @click="showSearchPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div>
    <!-- header end -->

    <div class="container">
      <!-- 查询条件  begin -->
      <van-popup v-model="SeachShow" position="top" class="search-popup" overlay-class="transparent-overlay">
        <div class="common-main box">
          <div class="spe-input">
            <van-cell v-if="showRy" title="选择人员" :value="renyuan" is-link @click="RenYuanshow = true" />
            <van-cell title="选择开始日期" :value="date" is-link @click="show = true" />
            <van-cell title="选择截止日期" :value="date1" is-link @click="show1 = true" />
            <van-calendar v-model="show" :min-date="minDate" :max-date="maxDate" color="#1890ff" @confirm="onConfirm" />
            <van-calendar v-model="show1" :min-date="minDate1" color="#1890ff" @confirm="onConfirm1" />
          </div>
          <div class="search-popup-btn">
            <van-button type="info" round size="large" plain @click="reset()">重置</van-button>
            <van-button type="info" round size="large" @click="getRzList()">搜索</van-button>
          </div>
        </div>
      </van-popup>
      <!-- 查询条件  end -->

      <van-collapse v-model="activeNames">
        <common-list
          ref="dida_list"
          class="common-list"
          :post-url="list.postUrl"
          :req-data="list.reqData"
          :data-name="list.dataName"
          :apptype-show="false"
          @returnData="getPrizeLog($event)"
        >
          <div v-if="rzlist && rzlist.length>0">
            <!-- 循环列表 begin -->
            <div v-for="(item,index) of rzlist" :key="index" class="content">
              <div class="common-main box">
                <van-collapse-item :name="index+1">
                  <template #title>
                    <div class="work-rizhi-date">
                      <van-checkbox v-model="item.checked" :disabled="item.spzt===3" @click="checkChange(item,index,item)" @click.stop.native="()=>{}" />
                      <h4>{{ formatDate12(item.rzsj) }} {{ getWeek(item.rzsj) }}</h4>
                    </div>
                  </template>
                  <div class="rzdetail-content">
                    <div v-for="(item1,index1) of item.rzmxArr" :key="index1" class="rzdetail-list">
                      <van-cell title="分类" :value="item1.gzxzmc" />
                      <van-cell v-if="item1.xmmc && item1.xmmc!=='请选择项目'" title="项目" :value="item1.xmmc" />
                      <div class="page-list block">
                        <div class="pagelist-left">内容</div>
                        <div class="pagelist-right">
                          <p>{{ item1.gznr }}</p>
                        </div>
                      </div>
                      <van-cell title="工作量(小时)" :value="item1.gzl" />
                      <van-cell title="审定工作量(小时)" :value="item1.sdgzl" />
                    </div>
                  </div>
                  <div class="txyj audit-opinion">
                    <div class="txyj-tit">
                      审批意见
                    </div>
                    <div class="txyj-content">
                      <textarea v-model="item.spyj" rows="2" readonly />
                    </div>
                  </div>
                </van-collapse-item>
              </div>
            </div>
          </div>
        </common-list>
      </van-collapse>
      <div v-if="pageStyles !== 0">
        <error-page
          :page-style="pageStyles"
          :show-button="false"
        />
      </div>
    </div>
    <!-- 循环列表 end -->
    <div v-if="rzlist && rzlist.length>0" class="foot-btn fixed">
      <ul>
        <li v-if="rzlist &&rzlist.length>0"><van-button type="info" round size="large" plain @click="checkAllRz()">全选</van-button></li>
        <li v-if="rzlist &&rzlist.length>0"><van-button type="info" round size="large" @click="thGzrz">退回</van-button></li>
      </ul>
    </div>
    <!-- 选择部门-人员弹窗 begin -->
    <van-popup v-model="RenYuanshow" round position="bottom">
      <van-cascader
        v-model="cascaderValue"
        class="cascader-flex"
        title="选择人员"
        :options="options"
        @close="RenYuanshow = false"
        @finish="onFinish"
      />
    </van-popup>
    <!-- 选择部门-人员弹窗 end -->

    <div class="common-space" />

    <!-- 底部按钮 begin -->
    <!-- <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large">取消审核</van-button></li>
      </ul>
    </div> -->
    <!-- 底部按钮 end -->
    <Backtop class="aside-fixed-bar" />
  </div>
</template>

<script land="script" scoped>
// import headerBar from '@/components/header/headerBar.vue'
import { getUserName } from '@/common/js/catch'
import commonList from '@/components/list/commonList1.vue'
import errorPage from '@/components/result-page/index'
import Backtop from '@/components/backtop/backtop' // 返回顶部
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'ShGzrzJL',
  components: {
    // headerBar,
    commonList,
    errorPage,
    Backtop
  },
  mixins: [pageRouter],
  data() {
    return {
      pageStyles: 1,
      show1: false,
      SeachShow: false,
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      date1: '请选择日期',
      headerData: {
        title: '审核记录',
        leftUrl: 'back',
        leftText: ' '
      },
      maxDate: new Date(2100, 0, 1),
      minDate: new Date(1999, 0, 1),
      minDate1: new Date(1999, 0, 1),
      date: '请选择日期',
      renyuan: '请选择人员',
      show: false,
      RenYuanshow: false,
      activeNames: [],
      rzlist: [],
      cascaderValue: '',
      options: [],
      checked: false,
      checkRzList: [],
      showRy: false,
      checkAll: false
    }
  },
  activated() {
    if (this.$route.params.ry) {
      this.showRy = false
      this.rymc = this.$route.params.ry.ryid
      this.renyuan = this.$route.params.ry.rymc
    } else {
      this.showRy = true
    }
    this.getGzrzLists()
    this.getShryList()
  },
  created() {
    if (this.$route.params.ry) {
      this.showRy = false
      this.rymc = this.$route.params.ry.ryid
      this.renyuan = this.$route.params.ry.rymc
    } else {
      this.showRy = true
    }
    this.getGzrzLists()
    this.getShryList()
  },
  mounted() {
    if (this.$route.params.ry) {
      this.getRzList()
    }
  },
  methods: {
    checkAllRz() {
      this.checkAll = !this.checkAll
      if (this.checkAll) {
        const arr = []
        for (let i = 0; i < this.rzlist.length; i++) {
          if (this.rzlist[i].spzt !== 3) {
            this.$set(this.rzlist[i], 'checked', true)
            arr.push(this.rzlist[i])
          }
        }
        this.checkRzList = JSON.parse(JSON.stringify(arr))
      } else {
        for (let i = 0; i < this.rzlist.length; i++) {
          if (this.rzlist[i].spzt !== 3) {
            this.$set(this.rzlist[i], 'checked', false)
          }
        }
        this.checkRzList = []
      }
      // this.rzlist.splice(0, 1, this.rzlist[0])
    },
    reset() {
      this.renyuan = '请选择人员'
      this.date = '请选择日期'
      this.date1 = '请选择日期'
      this.ryid = ''
      this.ksrq = ''
      this.jzrq = ''
    },
    goTbGzrz() {
      this.$router.back()
    },
    checkChange(item, index) {
      if (item.spzt === 3) {
        this.$toast('当前日志已归档，不可操作！')
        return
      }
      let stata = false
      for (let i = 0; i < this.checkRzList.length; i++) {
        if (this.checkRzList[i].id === item.id) {
          stata = true
          break
        }
      }
      if (stata) {
        this.checkRzList.splice(index, 1)
      } else {
        this.checkRzList.push(item)
      }
    },
    getPrizeLog(res) {
      if (res && res[0]) {
        this.rzlist = res
        if (this.rzlist.length > 0) {
          this.pageStyles = 0
        } else {
          this.pageStyles = 1
        }
        this.rzlist.splice(0, 1, this.rzlist[0])
      }
      this.checkAll = false
    },
    getGzrzLists() {
      this.list.postUrl = 'erp/getGzrzAction'
      this.list.reqData = {
        ksrq: this.ksrq,
        jzrq: this.jsrq,
        ryid: this.rymc,
        shbz: this.shztValue
      }
      this.list.dataName = 'gzrzArr'
    },
    getShryList() {
      this.options = []
      this.$store.dispatch('erp/getShryAction', {
        ryid: getUserName()
      }).then(response => {
        for (let j = 0; j < response.length; j++) {
          this.options.push({
            text: response[j].rymc,
            value: response[j].ryid
          })
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    getRzList() {
      this.SeachShow = false
      const reqData = {
        ksrq: this.ksrq,
        jzrq: this.jsrq,
        ryid: this.rymc,
        shbz: [2, 3]
      }
      this.$refs.dida_list.onRefresh(reqData)
      // this.$store.dispatch('erp/getGzrzAction', {
      //   ksrq: this.ksrq,
      //   jzrq: this.jsrq,
      //   ryid: this.rymc,
      //   shbz: [2]
      // }).then(res => {
      //   this.rzlist = res
      // }).catch(error => {
      //   this.$toast(error)
      // })
    },
    thGzrz() {
      if (!this.checkRzList || this.checkRzList.length === 0) {
        this.$toast('请先选择日志！')
        return
      }
      const rqarr = []
      for (let i = 0; i < this.checkRzList.length; i++) {
        rqarr.push(this.checkRzList[i].rzsj)
      }
      this.$store.dispatch('erp/backShrzAction', {
        ryid: this.rymc,
        rqArr: rqarr
      }).then(res => {
        this.$toast('退回日志成功！')
        this.getRzList()
      }).catch(error => {
        this.$toast(error)
      })
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    formatDate12(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    },
    getWeek(date) {
      date = date + ''
      const str = date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
      const temp = new Date(str)
      if (temp.getDay() === 1) {
        return '周一'
      } else if (temp.getDay() === 2) {
        return '周二'
      } else if (temp.getDay() === 3) {
        return '周三'
      } else if (temp.getDay() === 4) {
        return '周四'
      } else if (temp.getDay() === 5) {
        return '周五'
      } else if (temp.getDay() === 6) {
        return '周六'
      } else if (temp.getDay() === 0) {
        return '周日'
      }
    },
    onConfirm(date) {
      this.ksrq = this.formatDate1(date)
      this.date = this.formatDate(date)
      this.minDate1 = date
      this.show = false
    },
    onConfirm1(date) {
      this.jsrq = this.formatDate1(date)
      this.date1 = this.formatDate(date)
      this.maxDate = date
      this.show1 = false
    },
    // 全部选项选择完毕后，会触发 finish 事件
    onFinish({ selectedOptions }) {
      this.rymc = selectedOptions[0].value
      this.RenYuanshow = false
      this.renyuan = selectedOptions.map((option) => option.text).join('/')
      // if (this.ksrq && this.jsrq) {
      //   this.getRzList()
      // }
    },
    goXzrzCyy() {
      this.$router.push({
        name: 'XzrzCyy'
      })
    },
    goShGzrzYl() {
      this.$router.push({
        name: 'ShGzrzYl'
      })
    },
    showSearchPopup() {
      this.SeachShow = true
    }
  }
}
</script>

<style scoped>
.common-main{padding: 5px 10px;}
.van-collapse-item--border::after{display: none;}
.van-cell::after{display: none;}
/deep/.van-field__label{width: auto;}
</style>
